<template>
<div>
    <!--农场-->
    <div class="panel panel-info">
        <div class="panel-body">
            <div>实时监测</div>
            <div>
                <stack-button :initData="initData" v-show="buttonVisible" :callBackData.sync="callBackData"></stack-button>
            </div>
            <!--传感器详细信息-->
            <div>

            </div>
        </div>
    </div>
</div>
</template>

<script>
import stackButton from '@/components/stackButton.vue'
export default {
    components:{
        stackButton
    },
    data() {
        return {
            buttonVisible:true,
            initData:[
                {
                    url:"sceneFarm",
                    title:"农场"
                },
                {
                    url:"greenHouse",
                     title:"大棚"
                },
                {
                    url:"equip",
                    title:"设备"
                },
                {
                    url:"sensor",
                    title:"传感器", 
                    callback:true
                },
            ],
            callBackData:{}
        }
    },
    watch:{
        callBackData(news){
           this.loginSensorInfData(news.id);
        }
    },
    methods: {
        loginSensorInfData(id){
            var vm=this;
            vm.$axios.post(vm.HOST+"",{id:id}).then(success =>{}).catch(error =>{})
        }
    }
}
</script>

<style>

</style>



