<template>
<div class="panel panel-default">
    <div class="panel-heading">
        <div>
            <h4><span v-for="(item,index) in typeCheckData" :key="index" v-html="item.name+'&nbsp;&nbsp;&gt;&nbsp;&nbsp;'" v-show="index<typenum"></span>
                <span v-html="initData[typenum].title"></span><a v-if="typenum!=0" class="btn btn-link pull-right" @click="loginItems(-1)">返回上一级</a></h4>
        </div>
    </div>
    <div class="panel-body">
        <div v-if="items.length>0">
            <div class="col-sm-2 col-md-1 col-lg-1" v-for="(item,index) in items" :key="index">
                <button class="btn btn-default" @click="loginItems(1,item)"  v-html="item.name"></button>
            </div>
        </div>
        <div v-show="items.length<=0" class="col-sm-12 txt-center cursor-pointer logindata-error">
            未查找到数据，点击请重试
        </div>
    </div>
</div>
</template>

<script>
export default {
    data() {
        return {
            typenum: 0,
            items: [{
                id: 1,
                name: 1
            }],
            typeCheckData: []
        }
    },
    props: {
        initData: Array,
        callBackData:Object
    },
    methods: {
        loginItems(direction, item = "") {
            var vm = this;
            var typenum = vm.typenum;
            if (vm.initData[typenum].callback ===true&&direction>0) {
                vm.$emit("update:callBackData",{id:item.id,type:typenum});
            }
            if (item != "") {
                vm.typeCheckData[vm.typenum] = item;
            }
            if (direction > 0) {
                vm.typenum += 1;
            } else if(direction < 0) {
                vm.typenum -= 1;
            }
            typenum = vm.typenum;
            if (typenum <= vm.initData.length - 1) {
                vm.loginThisItems(vm.initData[typenum].url, item != '' ? item.id : (vm.typenum == 0 ? "" : vm.typeCheckData[typenum].id))
            } else {
                vm.typenum = vm.initData.length - 1;
                typenum = vm.typenum;
            }
        },
        loginThisItems(url, id = "") {
            var vm = this;
            vm.$axios.post(vm.HOST + url, id != "" ? {
                id: id
            } : "").then(success => {
                vm.items = success.data;
            }).catch(error => {})
        }
    }
}
</script>

<style scoped>
.logindata-error {
    font-size: 18px;
    color: rgb(243, 29, 29);
}
</style>



